.profile-icon {
    width: 35px;              /* Adjust size */
    height: 35px;             /* Adjust size */
    border-radius: 50%;       /* Make it circular */
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-weight: bold;
    font-size: 18px;          /* Adjust font size */
    text-transform: uppercase; /* Make initials uppercase */
}
